html {
  font-family: "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro", "游ゴシック体", "メイリオ ", "Roboto Bold", "03 スマートフォント", "Braisetto Regular", "Thonburi Bold", sans-serif;
  font-size: 62.5%; }

body {
  font-size: 1.6rem;
  color: #0A0A0A;
  word-wrap: break-word; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 2em;
  margin-bottom: 1em;
  line-height: 1.3; }

h1 + h2,
h2 + h3,
h3 + h4,
h4 + h5,
h5 + h6 {
  margin-top: 1em; }

h1 {
  font-size: 2.24rem; }

h2 {
  font-size: 1.92rem;
  font-weight: normal;
  padding-bottom: 10px;
  border-bottom: 1px solid #0A0A0A; }

h3 {
  font-size: 1.6rem;
  font-weight: bold; }

h4 {
  font-size: 1.28rem; }

h5 {
  font-size: 1.12rem; }

h6 {
  font-size: 0.96rem; }

a {
  color: #007bff;
  transition: all .5s ease; }
  a:hover {
    color: #6610f2; }

p {
  line-height: 2; }

ul,
ol {
  margin-left: 0;
  padding-left: 0;
  list-style-type: none; }
  ul li,
  ol li {
    position: relative;
    padding-left: 13px; }
    ul li:before,
    ol li:before {
      position: absolute;
      content: "";
      display: block;
      background-image: url("../../assets/images/icon-list.png");
      background-size: contain;
      background-repeat: no-repeat;
      width: 6px;
      height: 10px;
      top: 6px;
      left: 0; }

blockquote {
  background: #f4f4f4;
  padding: 1em;
  margin-top: 2em;
  margin-bottom: 1.5em; }
  blockquote p {
    font-size: 1.12rem;
    font-style: italic; }
    blockquote p:last-child {
      margin-bottom: 0; }

table {
  margin-bottom: 2em;
  border-collapse: collapse;
  border: 1px solid #ccc; }
  table a {
    color: #0A0A0A; }
    table a:hover, table a:visited, table a:focus, table a:active {
      color: black; }
  table td,
  table th {
    margin-bottom: 2em;
    border-collapse: collapse;
    border: 1px solid #ccc;
    padding: 5px; }
  table th {
    text-align: center; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom; }

figure {
  max-width: 100% !important; }

.wp-caption.aligncenter,
.wp-caption.alignnone {
  margin-bottom: 2em; }

.alignleft {
  float: left;
  margin-right: 1em; }

.alignright {
  float: right;
  margin-left: 1em; }

.aligncenter {
  margin: 0 auto; }

.wp-caption-text {
  font-size: 0.96rem; }

pre {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 5px; }
